$font-name: "OpenSans";
$font-family: $font-name, "Tahoma", sans-serif;
$font-weight-normal: normal;
$font-weight-bold: bold;

$white: #ffffff;
$white-two: #f1f1f1;
$white-three: #ececec;
$white-four: #fbfbfb;

$seafoam-blue: #63d1bb;
$seafoam-blue-two: #62d1ba;
$greany-blue: #56c3b2;
$emerald-blue: #46c1ae;

$light-grey: #e6e6e8;
$grey: #444444;

$black: #000000;

$mobile-width: 320px;
$tablet-width: 768px;
$desktop-width: 1150px;

$mobile-padding: 30px;
$tablet-padding: 50px;
$desktop-padding: 50px;

$video-shadow-hover: 0 6px 10px 0 transparentize($black, 0.9);
$video-shadow-active: 0 3px 5px 0 transparentize($black, 0.9);

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

$text-color: $grey;
$title-color: $black;
